import MobileDatePicker from '@mui/lab/MobileDatePicker'
import { Box, Card, Container, Grid, TextField } from '@mui/material'
import axios, { AxiosResponse } from 'axios'
import dayjs, { Dayjs } from 'dayjs'
import { useSnackbar } from 'notistack'
import { FC, useEffect, useRef, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'
import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import TextEditor from 'src/components/TextEditor'
import PageHeader from '../PageHeader'

interface RecruitAPIResponse {
  contents: string
  startAt: string
  semester?: string
  endAt?: string
  limit?: number
}

const Intro: FC<void> = () => {
  const club = {
    name: '동아리 이름!'
  }
  const params = useParams()
  const clubId = parseInt(params.id)
  const [content, setContent] = useState<string>('')
  const [startAt, setStartAt] = useState<Dayjs | null>(dayjs('2023-01-01T00:00:00'))
  const [endAt, setEndAt] = useState<Dayjs | null>(dayjs('2023-01-01T00:00:00'))
  const [limit, setLimit] = useState<number>(0)
  const [isEditMode, setIsEditMode] = useState<boolean>(false)
  const { enqueueSnackbar } = useSnackbar()
  const editorRef = useRef<any>(null)

  const handleSubmit = () => {
    if (endAt.isBefore(startAt)) {
      enqueueSnackbar('종료일이 시작일보다 빠를 수 없습니다.', { variant: 'error' })
      return
    }

    // 모집 종료일 기준으로 학기 나눔
    axios({
      method: isEditMode ? 'put' : 'post',
      url: `${process.env.REACT_APP_APIHOST}/club/recruit`,
      data: {
        clubId: clubId,
        contents: editorRef.current?.getInstance().getHTML(),
        startAt: startAt,
        endAt: endAt,
        limit: limit
      }
    })
      .then(res => {
        if (res.data.message === 'ok') {
          enqueueSnackbar('등록이 완료되었습니다.', { variant: 'success' })
        } else {
          throw new Error()
        }
      })
      .catch(() => {
        enqueueSnackbar('등록에 실패했습니다. 다시 시도하여 주십시오.', { variant: 'error' })
      })
  }

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/recruit?clubId=${clubId}`)
      .then((res: AxiosResponse<RecruitAPIResponse>) => {
        setContent(res.data.contents)
        setStartAt(dayjs(res.data.startAt))
        setEndAt(dayjs(res.data.endAt))
        setLimit(res.data.limit)
        setIsEditMode(true)
      })
  }, [])

  return (
    <>
      <Helmet>
        <title>소개 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club.name} link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Card>{content && <TextEditor content={content} editorRef={editorRef} />}</Card>
        <Box mt={2} mb={2}>
          <Card sx={{ padding: { xs: '20px', md: '30px' } }}>
            <Grid container justifyContent="center" spacing={2}>
              <Grid item sm={12} md={3}>
                <MobileDatePicker
                  label="모집 시작일"
                  inputFormat="yyyy/MM/dd"
                  value={startAt}
                  onChange={newValue => {
                    setStartAt(dayjs(newValue))
                  }}
                  renderInput={params => <TextField {...params} />}
                />
              </Grid>
              <Grid item sm={12} md={3}>
                <MobileDatePicker
                  label="모집 종료일"
                  inputFormat="yyyy/MM/dd"
                  value={endAt}
                  onChange={newValue => {
                    setEndAt(dayjs(newValue))
                  }}
                  renderInput={params => <TextField {...params} />}
                />
              </Grid>
              <Grid item sm={12} md={3}>
                <TextField
                  label="인원 제한(없으면 0)"
                  id="limit"
                  value={limit}
                  onChange={e => {
                    setLimit(parseInt(e.target.value))
                  }}
                ></TextField>
              </Grid>
            </Grid>
          </Card>
          <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText="등록 / 수정" />
        </Box>
      </Container>
    </>
  )
}

export default Intro
